const idMenu = document.getElementById('menu');

const nav = document.querySelector('.nav');
const navLinks = document.querySelector('.nav-links');

const btnTheme = document.querySelector('.button-theme');
const iconThemeLight = document.querySelector('.fa-moon');
const iconThemeDark = document.querySelector('.fa-sun');

emailjs.init('kPcbVCNMz3c7UtZY_');
const form = document.getElementById('form');
const formBtn = document.getElementById('form-btn');

idMenu.addEventListener('click', openNav);
btnTheme.addEventListener('click', changeTheme);
navLinks.addEventListener('click', linkClick);
form.addEventListener('submit', formSubmit);

localStorageDB();

function openNav(){
    nav.style = 'position: unset;';
    navLinks.classList.remove('hide');
}

function changeTheme(){
    document.body.classList.toggle('dark-theme');

    iconThemeLight.classList.toggle('hide');
    iconThemeDark.classList.toggle('hide');

    if(document.body.classList.contains('dark-theme')){
        localStorage.setItem('dark-theme', 'true');
    }else{
        localStorage.setItem('dark-theme', 'false');
    }
}

function localStorageDB(){
    if(localStorage.getItem('dark-theme') === 'true'){
        document.body.classList.add('dark-theme');
        iconThemeLight.classList.add('hide');
        iconThemeDark.classList.remove('hide');
    }else{
        document.body.classList.remove('dark-theme');
        iconThemeLight.classList.remove('hide');
        iconThemeDark.classList.add('hide');
    }
}

function linkClick(){    
    nav.style = 'position: fixed;';
    navLinks.classList.add('hide');
}

function formSubmit(e){
    e.preventDefault();

    const serviceID = 'default_service';
    const templateID = 'template_r7p0oyu';

    emailjs.sendForm(serviceID, templateID, this)
        .then(() => {
            alert('Enviado!');

            form.reset();
        }, (err) => {
            alert(JSON.stringify(err));
        });
}